import HeaderBack from '../Common/HeaderBack'

export default {
  components: {
    myHeader: HeaderBack,
  },
  data () {
    return {
      header: {
        HBL_icon:"icon_back",
        HBC_text:"常见问题",
        HBR_icon: " ",
      },
      isA: false,
      isB: false,
      isC: false,
      isD: false,
      isE: false,
      isF: false,
      isG: false,
      isH: false,
      isI: false,
      isJ: false,
      isK: false,
      isL: false,
      isM: false,
      isN: false,
      isO: false,
    }
  },
  methods: {
    toggle: function(index){
      if(index==1){
        this.isA = !this.isA;
      }else if(index==2){
        this.isB = !this.isB;
      }else if(index==3){
        this.isC = !this.isC;
      }else if(index==4){
        this.isD = !this.isD;
      }else if(index==5){
        this.isE = !this.isE;
      }else if(index==6){
        this.isF = !this.isF;
      }else if(index==7){
        this.isG = !this.isG;
      }else if(index==8){
        this.isH = !this.isH;
      }else if(index==9){
        this.isI = !this.isI;
      }else if(index==10){
        this.isJ = !this.isJ;
      }else if(index==11){
        this.isK = !this.isK;
      }else if(index==12){
        this.isL = !this.isL;
      }else if(index==13){
        this.isM = !this.isM;
      }else if(index==14){
        this.isN = !this.isN;
      }else if(index==15){
        this.isO = !this.isO;
      }
    },
  }
}



